/**
 * @module ui-bus-format-amount-ng
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBusFormatAmountKey from 'ui-bus-format-amount-ng';
 *
 * export const dependencyKeys = [
 *   uiBusFormatAmountKey,
 * ];
 *
 * // file: templates/template.ng.html
 * <ui-bus-format-amount
 * amount="$option.amount"
 * currency="$option.currency"
 * ></ui-bus-format-amount>
 *
 */
import angular from 'vendor-bb-angular';
import libBbCurrencyNgKey, { bbCurrencyRuleKey } from 'lib-bb-currency-ng';

import uiBusFormatAmountComponent from './format-amount.component';
import uiBusFormatAmountController from './format-amount.controller';

import { COMPONENT_KEY, CONTROLLER_KEY, MODULE_KEY } from './constants';

/**
 * @description The angular module name
 * @name default
 * @type {string}
 */
export default angular.module(MODULE_KEY, [libBbCurrencyNgKey])
  .component(COMPONENT_KEY, uiBusFormatAmountComponent)
  .controller(CONTROLLER_KEY, [
    bbCurrencyRuleKey,
    uiBusFormatAmountController,
  ])
  .name;



