import { CONTROLLER_KEY } from './constants';

/**
 * @name uiBusFormatAmountComponent
 * @type {object}
 * @description
 * Format Business Amount Component Object
 *
 * @property {string} amount Amount string
 * @property {string} currency Currency string
 */
const uiBusFormatAmountComponent = {
  bindings: {
    amount: '<',
    currency: '<',
  },
  template: `
    <span class="amount amount-plain" data-ng-if="$ctrl.isAmountSet()">
      <span class="amount-currency" data-ng-bind="$ctrl.currency"></span>
      <span 
        class="amount-value" 
        data-ng-bind="$ctrl.amount | currency : '' : $ctrl.decimalDigits"></span>
    </span>
  `,
  controller: CONTROLLER_KEY,
};

export default uiBusFormatAmountComponent;



